<template>
  <div class="showContent checkInfoContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <backSubMenu :backTitle="backTitle" :lineUnder="false"></backSubMenu>
    <div class="detail">
      <div class="left">
        <div class="infor_form_detail">
          <div>
          <!-- 公司名 -->
          <label>NIK</label>
          <input type="text" :value="resultData.NIK"  readonly>
        </div>
        <div>
          <!-- Nama -->
          <label>Name</label>
          <input type="text" :value="resultData.Nama" readonly>
        </div>
        <div>
          <!-- Gol Darah 血型 -->
          <label>Bloodtype</label>
          <input type="text" :value="resultData['Gol Darah']" readonly>
        </div>
        <div>
          <!-- Agama 宗教-->
          <label>Religion</label>
          <input type="text" :value="resultData.Agama" readonly>
        </div>
        <div>
          <!-- Jenis Kelamin 性别-->
          <label>Gender</label>
          <input type="text" :value="resultData['Jenis Kelamin']" readonly>
        </div>
        <div>
          <!-- Tempat/Tgl  出生地/日期 -->
          <label>Religion</label>
          <input type="text" :value="resultData['Tempat/Tgl Lahir']" readonly>
        </div>
         <div>
          <!-- Tempat/Tgl  出生地/日期 -->
          <label>Birthday</label>
          <input type="text" :value="resultData['Tempat/Tgl Lahir']" readonly>
        </div>
         <div>
          <!-- RT/RW  出生地/日期 -->
          <label>RT/RW</label>
          <input type="text" :value="resultData['RT/RW']" readonly>
        </div>
        <div>
          <!-- PROVINSI   -->
          <label>Province</label>
          <input type="text" :value="resultData.PROVINSI" readonly>
        </div>
        <div>
          <!-- CITY   -->
          <label>City</label>
          <input type="text" :value="resultData.CITY" readonly>
        </div>
        <div>
          <!-- Kel/Desa  Village 村   -->
          <label>Village</label>
          <input type="text" :value="resultData['Kel/Desa']" readonly>
        </div>
        <div>
          <!-- Kecamatan District 街道   -->
          <label>District</label>
          <input type="text" :value="resultData.Kecamatan" readonly>
        </div>
        <div>
          <!-- Alamat   Address 地址 -->
          <label>Address</label>
          <input type="text" :value="resultData.Alamat" readonly>
        </div>
        <div>
          <!-- Pekerjaan   Occupation 工作 -->
          <label>Occupation</label>
          <input type="text" :value="resultData.Pekerjaan" readonly>
        </div>
        <div>
          <!-- Status PerKawinan   婚姻状况 -->
          <label>Marital status</label>
          <input type="text" :value="resultData['Status PerKawinan']" readonly>
        </div>
        <div>
          <!-- Berlaku Hingga   Berlaku Hingga 有效期至 -->
          <label>ExpiryDate</label>
          <input type="text" :value="resultData['Berlaku Hingga']" readonly>
        </div>
        <div>
          <!-- Kewarganegaraan   nationality  -->
          <label>Nationality</label>
          <input type="text" :value="resultData.Kewarganegaraan" readonly>
        </div>
        </div>
      </div>
      <div class="right">
        <div class="infor_form_detail">
          <div>
            <!-- Time -->
            <label>Time</label>
            <input type="text" :value="$util.timestampToDay(record.created_at)" readonly>
          </div>
          <!-- <div>
            <label>Event</label>
            <input type="text" :value="detailData.name" readonly>
          </div> -->
          <div class="hasDot">
            <!-- Situation -->
            <label>Situation</label>
            <input type="text" :value="record.status" readonly>
            <span :class="record.status=='success'?'greenDot':'redDot'" id="statusDot">
              <i class="iconfont amd-dot"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import backSubMenu from "@/components/backSubMenu";
import { mapGetters } from "vuex";
import { fail } from "assert";

export default {
  data() {
    return {
      routekey: [
        "idCard",
        "idCardHistory;idCardHistoryDetail"
      ],
      backTitle: "Details",
      resultData: "",
      record:"",
    };
  },
  computed: {},
  components: {
    requestAndHisSubMenu,
    backSubMenu
  },
  methods: {
    getDetailData() {
      let params = this.$route.params;
      console.log(params);
      this.record=params.record;
      this.resultData = params.record.response.data;
      console.log(this.resultData);
    }
  },
  created() {
    if(this.$route.params.record){
      this.getDetailData();
    }else{
      this.$router.go(-1);
    }
  }
  
};
</script>
<style lang="less">
@import "../../css/checkInfor.less";
</style>





